export default {
  name: 'PersonAmount',
  props: {
    mainTitle: {
      type: String,
      required: true
    },
    itemTitleLeft: {
      type: String,
      required: true
    },
    itemTitleRight: {
      type: String,
      required: true
    },
    amountAdult: {
      type: Number,
      required: false
    },
    amountChild: {
      type: Number,
      required: false
    }
  },
  methods: {
    addAdult(){
      this.$emit('addAdult')
    },
    addChild(){
      this.$emit('addChild')
    },
    minusAdult(){
      if(this.amountAdult < 2) return
      this.$emit('minusAdult', this.amountAdult - 1)
    },
    minusChild(){
      if(this.amountChild === 0) return
      this.$emit('minusChild', this.amountChild - 1)
    }
  }
}
