import { getDay, parseISO } from 'date-fns'
import HistoryRemark from '@/components/common/HistoryRemark'
import HistoryContact from '@/components/common/HistoryContact'
export default {
  name: 'history_content_planeTicket',
  components: {
    HistoryRemark,
    HistoryContact
  },
  props: {
    history: {
      type: Object,
      require: true
    }
  },
  data(){
    return {
      toggleItem: false
    }
  },
  computed: {
    departureWeekDay(){
      return `週${['一','二','三','四','五','六','日'][getDay(parseISO(this.history.require.departureDate)) - 1]}`
    },
    terminalWeekDay(){
      return `週${['一','二','三','四','五','六','日'][getDay(parseISO(this.history.optional.terminalDate)) - 1]}`
    }
  }
}
