import { mapGetters, mapMutations } from 'vuex'
import historyPlaneTicket from './history_content_planeTicket.vue'
import historyHotel from './history_content_hotel.vue'
import historyRentCar from './history_content_rentCar.vue'
export default {
  name: 'historyContent',
  components: {
    historyPlaneTicket,
    historyHotel,
    historyRentCar
  },
  data(){
    return{
      tabs: ['機票', '飯店', '租車'],
      historyType: '機票',
      histories: [],
    }
  },
  computed:{
    ...mapGetters(['bookData', 'isLoading']),
    history_planeTicket(){
      return this.histories ? this.histories.filter(item => item.bookNumber.includes('票')) : []
    },
    history_hotel(){
      return this.histories ? this.histories.filter(item => item.bookNumber.includes('住')) : []
    },
    history_rentCar(){
      return this.histories ? this.histories.filter(item => item.bookNumber.includes('租')) : []
    }
  },
  mounted(){
    this.dataGet()
  },
  methods:{
    ...mapMutations(['showLoading', 'hideLoading']),
    dataGet(){
      this.showLoading()
      this.$store.dispatch('DATA_GET').then(() => {
        this.hideLoading()
        this.histories = this.bookData
      })
    },
    clickTab(name){
      this.historyType = name
      this.dataGet()
    }
  }
}
