export default {
  name: 'CustomList',
  props:{
    type: {
      type: String,
      required: true,
      default: 'text'
    },
    title: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: true
    },
  },
  data(){
    return{
      inputVal: ''
    }
  },
  watch: {
    inputVal(newVal, oldVal){
      if(newVal !== oldVal){
        this.$emit('customListInput', newVal)
      }
    }
  },
  methods: {
    
  }
}
